import { Descriptions, Modal } from "antd";
import React from "react";

function OrderDetail({ dataContact, isModalVisible, handleCancel }) {
  return (
    <Modal
      width="60%"
      style={{ top: 10 }}
      title={<h4 className="m-0">Thng tin chi tit</h4>}
      visible={isModalVisible}
      onCancel={handleCancel}
      footer={false}
    >
      <Descriptions title="Thng tin khch hng" layout="vertical">
        <Descriptions.Item labelStyle={{ fontWeight: 600 }} label="Tn khch hng">
          {dataContact?.name}
        </Descriptions.Item>
        <Descriptions.Item labelStyle={{ fontWeight: 600 }} label="Email">
          {dataContact?.email}
        </Descriptions.Item>
        <Descriptions.Item labelStyle={{ fontWeight: 600 }} label="S in thoi">
          {dataContact?.phone}
        </Descriptions.Item>
      </Descriptions>
      <Descriptions title="Ni dung">
        <Descriptions.Item label="">{dataContact?.content}</Descriptions.Item>
      </Descriptions>
    </Modal>
  );
}

export default OrderDetail;
